.hidden {
  display: none !important;
}

label {
  font-weight: normal;
}

body {
  background-color: #f3f0eb;
  color: #444444;
}

.no-gutter {
  padding-right: 0;
  padding-left: 0;
  margin-left: 0;
  margin-right: 0;
}

.spinner {
  position: fixed;
  z-index: 999;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

a:focus, a:hover {
  text-decoration: none;
}

html, body {
  margin: 0;
  height: 100%;
}


